/* -------- GRID -------- */
/*
 * This file define main grid layout
 * and width for each section
 */
#main-container-inner {
  display: block;
  width: 100%;
  height: 100%;
}
@media (min-width: 768px) {
  #main-container-inner {
    display: grid;
    grid-template-columns: 90px 60px fit-content(100%) auto;
    justify-content: stretch;
    height: 100vh;
    overflow: hidden;
  }
}
@media (min-width: 1280px) {
  #main-container-inner {
    grid-template-columns: 90px 70px fit-content(100%) auto;
  }
}
@media (min-width: 1450px) {
  #main-container-inner {
    grid-template-columns: 90px 70px fit-content(100%) auto;
  }
}
#main-container-inner.trees-panel--hidden {
  grid-template-columns: 90px 60px auto;
}
@media (min-width: 1280px) {
  #main-container-inner.trees-panel--hidden {
    grid-template-columns: 90px 70px auto;
  }
}
@media (min-width: 1450px) {
  #main-container-inner.trees-panel--hidden {
    grid-template-columns: 90px 70px auto;
  }
}
#main-container-inner #user-panel {
  position: relative;
  width: 100%;
}
#main-container-inner #admin-menu {
  position: relative;
  width: 100%;
}
#main-container-inner #main-trees {
  position: relative;
  width: 270px;
}
@media (min-width: 1280px) {
  #main-container-inner #main-trees {
    width: 270px;
  }
}
@media (min-width: 1450px) {
  #main-container-inner #main-trees {
    width: 350px;
  }
}
@media (min-width: 1920px) {
  #main-container-inner #main-trees {
    width: 450px;
  }
}
@media (max-width: 767px) {
  #main-container-inner .navigation-container-section {
    position: fixed !important;
  }
}
#main-container-inner #main-content {
  display: block;
  width: 100vw;
  z-index: 0;
  position: relative;
  overflow-y: auto;
  --webkit-overflow-scrolling: touch;
}
@media (min-width: 768px) {
  #main-container-inner #main-content {
    width: 100%;
  }
}
#main-container-inner #main-content #canvasloader-container {
  position: fixed;
  top: 50%;
  left: 50%;
  margin: -20px 0 0 -20px;
  z-index: 12;
}
#main-container-inner #main-content #back-top-button {
  display: none;
  position: fixed;
  right: 15px;
  bottom: 15px;
  width: 36px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  background: var(--contrasted-bg);
  border: 1px solid #cbcbcb;
  color: var(--global-text-lighter-color);
  cursor: pointer;
  z-index: 3;
}
#main-container-inner #main-content #back-top-button:hover {
  background: #e6e6e6;
  border-color: #b2b2b2;
}
@media (min-width: 960px) {
  #main-container-inner #main-content #back-top-button {
    display: block;
  }
}
#main-container-inner.trees-panel--minified #main-trees {
  width: 0;
}
#main-content-overlay {
  display: none;
}
@media (max-width: 767px) {
  #main-content-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: #000;
    opacity: 0;
    z-index: 5;
  }
  #main-content-overlay.open {
    display: block;
    opacity: 0.5;
  }
}
/* -------- Dashboard -------- */
#latest-sources {
  text-align: center;
}
.latest-source {
  display: inline-block;
  max-width: 150px;
  vertical-align: top;
  margin: 10px 15px 20px;
}
.latest-source figure {
  position: relative;
  display: block;
  width: 150px;
  height: 150px;
}
.latest-source figure .no-thumb {
  border-radius: 75px;
  width: 150px;
  height: 150px;
  background-color: #CCC;
  display: block;
  transform-origin: 50% 50%;
  transition: transform ease 0.3s;
}
.latest-source figure img {
  display: block;
}
.latest-source figure img.source-image {
  border-radius: 75px;
  transform-origin: 50% 50%;
  transition: transform ease 0.3s;
}
.latest-source figure .user-image {
  border-radius: 30px;
  position: absolute;
  bottom: 0;
  right: 0;
  border: 5px solid #F8F8F8;
}
.latest-source .source-title,
.latest-source .source-update-date {
  color: var(--admin-entries-subnav-text-color);
  font-size: 11px;
  text-decoration: none;
  margin: 0px;
  padding: 5px 0px;
}
.latest-source .source-title {
  padding-top: 15px;
  border-bottom: 1px dashed #CCC;
  font-weight: bold;
}
.latest-source > a:hover {
  text-decoration: none;
}
.latest-source > a:hover .no-thumb,
.latest-source > a:hover .source-image {
  transform: scale(0.9,0.9);
}
.latest-source > a:hover .source-title,
.latest-source > a:hover .source-update-date {
  text-decoration: none;
  color: #333;
}
